import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../../services/main-services.service';
import { AlertController, ModalController } from "@ionic/angular";
import { AddPriorityModalComponent } from '.././add-priority-modal/add-priority-modal.component';
import { EditPriorityModalComponent } from ".././edit-priority-modal/edit-priority-modal.component";
import { ToastrService } from 'ngx-toastr';
import $ from 'jquery';
import { async } from 'rxjs/internal/scheduler/async';
import { LoaderService } from 'src/app/services/loader.service';



@Component({
  selector: 'app-priorities-list',
  templateUrl: './priorities-list.page.html',
  styleUrls: ['./priorities-list.page.scss'],
})
export class PrioritiesListPage implements OnInit {
  PrioritiesList;
  sucmsg;
  days; 
  list=[];
  

  constructor(private priorityData: MainServicesService,private modalController: ModalController,  
    private alertCtrl: AlertController, private toastr: ToastrService, private loader:LoaderService) { }

  ngOnInit() {
  }

  ionViewWillEnter(){
    this.list=[];
    this.priorityData.getPrioritiesList().subscribe(
      (result) =>{
        this.PrioritiesList = result['priorities'];


    this.PrioritiesList.forEach((data) => {
          this.list.push({
            _id: data._id,
            name      : data.name,
            time_line  :  data.time_line>=24 ? Math.floor(data.time_line / 24)+'d'+ ' '+ data.time_line % 24+'h' : data.time_line+'h'           

          })
        });
      }
    )
  }


  async openModal() {
    const modal = await this.modalController.create({
      component: AddPriorityModalComponent,
      cssClass: 'my-custom-priority-class'
    });

    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];

    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
    
  }

  async openeditModal(list) {
    const modal = await this.modalController.create({
      component: EditPriorityModalComponent,
      componentProps: {
        _id: list._id,
      },
      cssClass: 'my-edit-form-class'
    });

    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];
    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
    
  }

  async presentAlert(id) {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-alert-class',
      subHeader: 'Do you really want to delete it ?',
      buttons: [{
        text: 'Cancel'
      }, {
        text: 'yes',
        handler: () => {
          this. delete_priority(id)
        }
      }]
    });
  
    await alert.present();
  }
  

  delete_priority(id){
    this.priorityData.deletePriority(id).subscribe(
      (result) =>{
        this.sucmsg = result['message'];
        if (result['success'] == true) {
        this.toastr.success(this.sucmsg);
          this.ionViewWillEnter();
        }
      }
    )
  }


}
