import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ModalController } from "@ionic/angular";
import { NavParams } from '@ionic/angular';
import { MainServicesService } from './../../../services/main-services.service';
import $ from 'jquery';
import { LoaderService } from 'src/app/services/loader.service';



@Component({
  selector: 'app-edit-priority-modal',
  templateUrl: './edit-priority-modal.component.html',
  styleUrls: ['./edit-priority-modal.component.scss'],
})
export class EditPriorityModalComponent implements OnInit {
  @Input() _id: any;
  getId = this.navParams.get('_id');
  getData;
  editPriorityForm:FormGroup;
  showBtn;
  line_value_name="none";
  valuebranchName = true;
  valuebranchDesc = true;
  line_value_desc="none";
  errMsg;
  pnameRight:boolean=false;
  timelineRight:boolean=false;


  constructor(private formbuilder: FormBuilder, private modalController: ModalController,private priorityData: MainServicesService,
    public navParams: NavParams, private loader:LoaderService) {
      this.editPriorityForm = this.formbuilder.group({
        name: ['',Validators.required],
        time_line:['',Validators.required]
      })
     }

  ngOnInit() {}
  
  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

  editBranchName(){
    this.showBtn = true;
    this.line_value_name = "line";
    this.valuebranchName = false;
  }
  InsertPname(){
    $('.name-edit-pencil-icon').hide();
    this.pnameRight=true;
  }

  
  editBranchDesc(){
    this.showBtn = true;
    this.line_value_desc = "line";
    this.valuebranchDesc = false;
  }
  Inserttimeline(){
    $('.desc-edit-pencil-icon').hide();
    this.timelineRight=true;
  }

  ionViewWillEnter(){
  
    
    this.priorityData.editPriority(this.getId).subscribe(
      (result)=>{
        this.getData = result['priority'];

        this.editPriorityForm = this.formbuilder.group({
          name: [this.getData.name],
          time_line: [this.getData.time_line]
         })
      }
    )
  }

  updatePriority(value){

    this.priorityData.updatePriority(this.getId,value).subscribe(
      (result)=>{
        this.modalController.dismiss(result, 'success');
      },
      (error)=>{
        console.log(error);
        this.errMsg = error['error']['error'];
        if(this.errMsg){
          $('#errors').fadeIn(1000);
        }
      }
    )
  }

}
