import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController } from "@ionic/angular";
import $ from 'jquery';




@Component({
  selector: 'app-add-priority-modal',
  templateUrl: './add-priority-modal.component.html',
  styleUrls: ['./add-priority-modal.component.scss'],
})
export class AddPriorityModalComponent implements OnInit {
  priorityName;
  duration;
  errMsg;

  constructor( private priorityData: MainServicesService, private modalController: ModalController) { }

  ngOnInit() {}

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }
  
  priority_name(e){
    this.priorityName = e.target.value
  }

  timeline(e){
    this.duration = e.target.value
  }

  submit(){
    let addPriority = {
      name : this.priorityName,
      time_line : this.duration
    }
    
    this.priorityData.addPriorities(addPriority).subscribe(
      (result)=>{
        this.modalController.dismiss(result, 'success');
      },
      (error)=>{
        console.log(error);
        this.errMsg = error['error']['error']['name'];
        if(this.errMsg){
          $('#errors').fadeIn(1000);
        }        
    
      }
    )

  }

}
